export default {
	/**
	 *  "yyyy-MM-dd HH:mm:ss"
	 * time:Thu Apr 15 2021 14:23:24 GMT+0800   
	 */
	getDate(time){
		let y = time.getFullYear();
		let MM = time.getMonth() + 1;
		MM = MM < 10 ? ('0' + MM) : MM;//0
		let d = time.getDate();
		d = d < 10 ? ('0' + d) : d;//0
		let h = time.getHours();
		h = h < 10 ? ('0' + h) : h;//0
		let m = time.getMinutes();
		m = m < 10 ? ('0' + m) : m;//0
		let s = time.getSeconds();
		s = s < 10 ? ('0' + s) : s;//0
		return y + '-' + MM + '-' + d + ' ' + h + ':' + m+ ':' + s; //
	}
}